import React from 'react';
import { IonCol, IonHeader, IonPage, IonTitle, IonToolbar, IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCardContent, IonItem, IonIcon, IonLabel, IonButton, IonRippleEffect  } from '@ionic/react';

//Icons

import { location, expand, bed } from 'ionicons/icons';

const root = {
    marginBottom: 20,
    boxShadow: '0 5px 10px rgba(154,160,185,.05), 0 15px 40px rgba(166,173,201,.2)'
  };
const title__price = {
  display:'flex',
    justifyContent:'space-between',
    alignItems:'center'
}

const locationPlace = {
  display:'flex',
  alignContent:'center',
  marginBottom: '10px'
}

const flex = {
  display: 'flex'
}
  
const typo = {
  lineHeight:1.7
}

const bedrooms = {
  display:'flex',
  marginLeft: '20px'
}


function Houses(props) {
 
        return (
            <IonCol size-sm="12" size-md="3" size="12">
                <IonCard style={{margin:'0px'}} style={root} className="ion-activatable ripple-parent" >
                <img src={props.imageUrl} ></img>
                <IonCardHeader style={title__price}>
                <IonCardTitle style={typo}>{props.title}</IonCardTitle>
                <IonCardTitle>₹{props.price}</IonCardTitle>
                </IonCardHeader>

                 <IonCardContent>
                

                 <div style={locationPlace}>
                <IonIcon icon={location} className="icon" /> 
                <p variant="body2" style={typo} color="textSecondary" component="p">
                {props.location}
                </p>
                </div>

                 <div style={flex}>
                 <div style={flex}>
                 <IonIcon icon={expand} className="icon"  /> 
                 &nbsp;
                  <p variant="body2" style={typo} color="textSecondary" component="p">
                  {props.sqft} sqft
                  </p>
                  </div>

                <div style={bedrooms}>
                <IonIcon icon={bed} className="icon"  /> 
                &nbsp;
                <p variant="body2" style={typo} color="textSecondary" component="p">
                {props.bedrooms} bedrooms
                </p>
                </div>
                </div>
           
                </IonCardContent>
                
                <IonRippleEffect type="bounded"></IonRippleEffect>
                </IonCard>
                </IonCol>
        )
    
}

export default Houses
